<template>
  <v-card
    flat
    id="ghx-modal-id"
    class="ghx-audigy-app-modal elevation-0"
    :class="[
      {
        'no-bg':
          is_empty(theme.backgroundColor) || is_empty(theme.backgroundImage),
      },
    ]"
  >
    <v-col
      class="ghx-modal-btn-wrapper d-flex flex-row-reverse pt-6 pb-0 pr-6 col-sm-hide col-xs-hide"
      cols="12"
    >
      <v-btn
        @click="onClickExit"
        class="ghx-modal-btn-close"
        :class="{ hidden: isShowSaveFormPage && !isExitClicked }"
        small
        depressed
        fab
        text
      >
        <v-icon class="ghx-btn__icon" aria-hidden size="34">mdi-close</v-icon>
      </v-btn>
    </v-col>
    <v-card class="ghx-audigy-app-content" flat>
      <v-row
        class="ghx-modal-header ma-0"
        :class="[`ghx-modal-header--${theme.headerStyle}`]"
      >
        <v-col
          cols="12"
          class="ghx-app-modal--topbar pa-0 col-md-5 col-lg-5 pb-4 pb-sm-4 pb-lg-6 pb-md-6 pb-xl-6"
        >
          <div class="ghx-stepper-title ghx-navy-color d-flex">
            <div>
              <span
                v-if="config.title"
                class="ghx-stepper-title__text align-center"
              >
                {{ config.title }}
              </span>
              <span class="ghx-stepper-title__branding--mobile mr-4 pr-0 pt-2 pb-0">
                <v-img
                  v-if="config.mobileLogo"
                  :src="config.mobileLogo"
                  contain
                  width="45"
                >
                </v-img>
              </span>
            </div>
            <div v-if="config.desktopLogo">
              <span
                class="ghx-stepper-title__branding"
                :class="{ 'mt-2': config.title }"
              >
                <v-img :src="config.desktopLogo" contain></v-img>
              </span>
            </div>
            <span
              v-if="el !== 1 || (el === 1 && selectedItem)"
              class="ghx-stepper-title__progress align-end px-4"
            >
              <v-progress-linear
                v-model="progressValue"
                color="navy"
                height="12"
              >
              </v-progress-linear
            ></span>
          </div>
          <div
            class="ghx-modal-btn-wrapper flex-row-reverse pt-6 pb-0 pr-6 pa-sm-0 pa-xs-0 col-lg-hide col-md-hide"
          >
            <v-btn
              @click="onClickExit"
              class="ghx-modal-btn-close"
              :class="{ hidden: isShowSaveFormPage && !isExitClicked }"
              small
              depressed
              fab
              text
            >
              <SvgIcons name="close" />
            </v-btn>
          </div>
        </v-col>
        <v-col
          cols="12"
          class="ghx-stepper--header-wrapper pl-md-6 pl-sm-0 pl-0 pt-0 pr-0 pb-0 col-md-7 col-lg-7"
        >
          <v-stepper class="ghx-stepper overflow-visible" alt-labels flat>
            <!-- STEPPER HEADER -->
            <v-stepper-header class="ghx-stepper--header">
              <template v-for="(item, i) in flowSelector">
                <v-stepper-step
                  v-if="
                    flowSelector.find((step) => step.step === item.step)
                      .order === item.order
                  "
                  :key="`${selectedFlow}-${i}`"
                  color="navy"
                  :step="item.step"
                  :complete="el > item.order || el === flowSelector.length"
                  class="ghx-stepper-step text-center white--text"
                  :class="[
                    'ghx-stepper-step' + item.step,
                    { 'ghx-stepper-step--active': el === item.order },
                  ]"
                >
                  <span v-html="item.header?.title"></span>
                  <v-divider
                    class="ghx-divider"
                    :class="[`ghx-divider--counter-${i + 1}`]"
                    :data-items="
                      onGetTotalItems('step', item.step, flowSelector).length
                    "
                    :key="`d-${selectedFlow}-${i}`"
                  >
                  </v-divider>
                </v-stepper-step>
              </template>
            </v-stepper-header>
          </v-stepper>
        </v-col>
      </v-row>
      <v-row class="ghx-stepper-wrapper mt-0">
        <v-col class="pa-0" cols="12">
          <v-stepper
            class="ghx-stepper elevation-0"
            alt-labels
            v-model="el"
            v-show="!isShowSaveFormPage"
          >
            <v-stepper-items v-if="flowSelector.length >= el">
              <v-stepper-content
                v-for="(page, i) in flowSelector"
                :key="`step-content-${i}`"
                v-model="page.order"
                class="ghx-stepper-content ghx-stepper-content--items py-0 pl-1 pr-2"
                :class="[
                  page.style
                    ? `ghx-stepper-content--${page.style}`
                    : 'ghx-stepper-content--style-1',
                  footerStyle
                    ? `ghx-stepper-content--footer-${footerStyle}`
                    : 'ghx-stepper-content--footer-style-1',
                  `ghx-stepper-content--${page.form?.type}`,
                  `page-${page.order}`,
                ]"
                :step="page.order"
                :style="[i + 1 === animationStartAt ? { opacity: 1 } : '']"
              >
                <div class="py-8 py-sm-12">
                  <v-fade-transition group>
                    <template v-if="page.form?.type === 'date'">
                      <PageDate
                        :key="`${page.form?.type}-${page.order}`"
                        :page="page"
                        :currentOrder="el"
                        @on-item-selected="onSelectItem"
                        @on-item-emptied="onEmptyItem('date')"
                      >
                      </PageDate>
                    </template>

                    <template v-if="page.form?.type === 'radio'">
                      <PageRadio
                        :key="`${page.form?.type}-${page.order}`"
                        :page="page"
                        :currentOrder="el"
                        @on-item-selected="onSelectItem"
                        @on-item-emptied="onEmptyItem('radio')"
                      >
                      </PageRadio>
                    </template>

                    <template
                      v-if="
                        page.form?.type === 'info1' ||
                        page.form?.type === 'info2' ||
                        page.form?.type === 'info3'
                      "
                    >
                      <PageInstruction
                        :key="`${page.form?.type}-${page.order}`"
                        :page="page"
                        :currentOrder="el"
                        :version="getVersion(page.form?.type)"
                        @on-save-spot="onSaveSpotForm(false)"
                      >
                      </PageInstruction>
                    </template>

                    <template v-if="page.form?.type === 'init'">
                      <PageInit
                        :key="`${page.form?.type}-${page.order}`"
                        :page="page"
                      >
                      </PageInit>
                    </template>

                    <template v-if="page.form?.type === 'screener'">
                      <PageScreener
                        :key="`${page.form?.type}-${page.order}`"
                        :ref="`screener${page.order}${page.form?.hz}`"
                        :page="page"
                        :currentOrder="el"
                        @on-item-selected="onSelectItem"
                        @on-item-emptied="onEmptyItem('screener')"
                      >
                      </PageScreener>
                    </template>

                    <template v-if="page.form?.type === 'result'">
                      <PageResult
                        :key="`${page.form?.type}-${page.order}`"
                        :page="page"
                        :theme="theme"
                        :currentOrder="el"
                      >
                      </PageResult>
                    </template>

                    <template v-if="page.form?.type === 'video'">
                      <PageVideo
                        :key="`${page.form?.type}-${page.order}`"
                        :page="page"
                        :currentOrder="el"
                        @video-start="handleVideoStart"
                        ref="videoStep"
                      >
                      </PageVideo>
                    </template>

                    <template v-if="page.form?.type === 'end'">
                      <PageForm
                        :key="`${page.form?.type}-${page.order}`"
                        :page="page"
                        :currentOrder="el"
                        :isShow="isShowSaveFormPage"
                        :disabled="buttonLoading"
                        @on-reset="$emit('on-reset')"
                        @on-item-selected="onSelectItem"
                        @on-item-emptied="onEmptyItem('end')"
                        @on-hit-enter="onHitEnterOnInput($event)"
                      />
                    </template>
                  </v-fade-transition>
                </div>
              </v-stepper-content>

              <!-- <PageLoader
                v-if="hasResultPage"
                :active="hasResultPage && isShowLoader"
                :resultPage="hasResultPage"
                :footerStyle="footerStyle"
              /> -->

              <v-card
                v-if="el"
                class="ghx-stepper__action ghx-glass-background pa-0 d-flex"
                :class="[`ghx-stepper__action--${footerStyle}`]"
              >
                <v-col
                  col="12"
                  class="col-lg-12 col-sm-12 ghx-action-btn-container d-flex pa-0"
                >
                  <v-card-actions
                    class="ghx-stepper__action-wrapper pb-0 pt-4 px-0"
                  >
                    <v-btn
                      v-if="hideSaveSpotBtnInFinalPage"
                      @click="onSaveSpotForm(false)"
                      large
                      rounded
                      depressed
                      text
                      :ripple="false"
                      color="undefined"
                      class="ghx-btn ghx-btn--secondary ghx-btn--secondary--has-icon col-xs-hide col-sm-hide col-md-hide"
                      :class="[`ghx-btn--${btnStyle}`]"
                    >
                      <!-- <v-icon size="30" class="mr-1">mdi-bookmark-outline</v-icon> -->
                      <v-icon size="30" class="mr-1"
                        >mdi-bookmark-outline</v-icon
                      >
                      {{ currentPage?.btnSaveSpot || "Save My Spot And Exit" }}
                    </v-btn>

                    <v-btn
                      v-if="hideSaveSpotBtnInFinalPage"
                      @click="onSaveSpotForm(false)"
                      large
                      rounded
                      depressed
                      text
                      color="undefined"
                      :ripple="false"
                      class="ghx-btn ghx-btn--secondary ma-0 col-lg-hide"
                      :class="[
                        `ghx-btn--${btnStyle}`,
                        {'ghx-btn--mobile': mobileAndTabletCheck}]"
                    >
                      <v-icon size="30">mdi-bookmark-outline</v-icon>
                      Save My Spot...
                    </v-btn>

                    <v-spacer></v-spacer>

                    <v-btn
                      v-if="el > 1 && hideSaveSpotBtnInFinalPage"
                      @click="onPrevious"
                      large
                      rounded
                      depressed
                      text
                      :ripple="false"
                      color="undefined"
                      class="ghx-btn ghx-btn--secondary ghx-btn--secondary--has-icon ghx-btn--animate-left mr-4 col-xs-hide col-sm-hide"
                      :class="[`ghx-btn--${btnStyle}`]"
                    >
                      <v-icon right>mdi-arrow-left</v-icon>
                      Back
                    </v-btn>

                    <v-btn
                      v-if="el > 1 && hideSaveSpotBtnInFinalPage"
                      @click="onPrevious"
                      rounded
                      depressed
                      text
                      :ripple="false"
                      color="undefined"
                      class="ghx-btn ghx-btn--secondary col-md-hide col-lg-hide"
                      :class="[`ghx-btn--${btnStyle}`]"
                    >
                      <v-icon right size="30">mdi-arrow-left</v-icon>
                    </v-btn>

                    <v-btn
                      v-if="isFinalPage"
                      :loading="buttonLoading"
                      @click="onClickContinue(false)"
                      :disabled="!selectedItem"
                      large
                      rounded
                      depressed
                      color="undefined"
                      height="50"
                      class="ghx-btn ghx-btn--primary ghx-btn--primary--text col-xs-hide col-sm-hide"
                      :class="[`ghx-btn--${btnStyle}`]"
                    >
                    {{ currentPage.btnSaveSpot || "Get My Results" }}
                    </v-btn>

                    <v-btn
                      v-if="isFinalPage"
                      :loading="buttonLoading"
                      @click="onClickContinue(false)"
                      :disabled="!selectedItem"
                      rounded
                      depressed
                      color="undefined"
                      height="50"
                      class="ghx-btn ghx-btn--primary ghx-btn--primary--has-icon col-md-hide col-lg-hide"
                      :class="[`ghx-btn--${btnStyle}`,
                        {'ghx-btn--mobile': mobileAndTabletCheck}]"
                    >
                      <v-icon right>mdi-arrow-right</v-icon>
                    </v-btn>

                    <template v-else-if="isExcludedBtn">
                      <v-btn
                        id="ghx-btn--continue"
                        :loading="buttonLoading"
                        @click="onClickContinue(false)"
                        :disabled="!selectedItem"
                        large
                        rounded
                        depressed
                        color="undefined"
                        height="50"
                        class="ghx-btn ghx-btn--primary ghx-btn--primary--has-icon ghx-btn--primary--text ghx-btn--animate-right pl-6 col-xs-hide col-sm-hide"
                        :class="[`ghx-btn--${btnStyle}`]"
                      >
                        {{ currentPage.btnContinue || "Continue" }}
                        <v-icon right>mdi-arrow-right</v-icon>
                      </v-btn>

                      <v-btn
                        id="ghx-btn--continue"
                        :loading="buttonLoading"
                        @click="onClickContinue(false)"
                        :disabled="!selectedItem"
                        rounded
                        depressed
                        color="undefined"
                        height="50"
                        class="ghx-btn ghx-btn--primary ghx-btn--primary--has-icon col-md-hide col-lg-hide"
                        :class="[`ghx-btn--${btnStyle}`,
                        {'ghx-btn--mobile': mobileAndTabletCheck}]"
                      >
                        <v-icon right>mdi-arrow-right</v-icon>
                      </v-btn>
                    </template>

                    <template v-else>
                      <v-btn
                        id="ghx-btn--continue"
                        @click="onClickContinue(true)"
                        :loading="buttonLoading"
                        large
                        rounded
                        depressed
                        color="undefined"
                        height="50"
                        class="ghx-btn ghx-btn--primary ghx-btn--primary--has-icon ghx-btn--primary--text ghx-btn--animate-right pl-6 col-xs-hide col-sm-hide"
                        :class="[`ghx-btn--${btnStyle}`]"
                      >
                        {{ currentPage.btnContinue || "Continue" }}
                        <v-icon right>mdi-arrow-right</v-icon>
                      </v-btn>
                      <v-btn
                        id="ghx-btn--continue"
                        @click="onClickContinue(true)"
                        :loading="buttonLoading"
                        rounded
                        depressed
                        color="undefined"
                        height="50"
                        class="ghx-btn ghx-btn--primary ghx-btn--primary--has-icon col-md-hide col-lg-hide"
                        :class="[`ghx-btn--${btnStyle}`,
                        {'ghx-btn--mobile': mobileAndTabletCheck}]"
                      >
                        <v-icon right>mdi-arrow-right</v-icon>
                      </v-btn>
                    </template>
                  </v-card-actions>
                </v-col>
              </v-card>
            </v-stepper-items>

            <v-stepper-items v-else-if="isShowFinalPage">
              <v-col col="12" class="pa-0 ghx-stepper-content">
                <ul>
                  <li v-for="(value, name) in finalResult.data" :key="name">
                    <span v-if="typeof value === 'object'">
                      <list-comp :item="value"></list-comp>
                    </span>
                    <span v-else> {{ value }} </span>
                  </li>
                </ul>
              </v-col>

              <v-card
                class="ghx-stepper__action ghx-glass-background pa-0 d-flex"
                :class="[`ghx-stepper__action--${footerStyle}`]"
              >
                <v-col col="12" class="ghx-action-btn-container d-flex pa-0">
                  <v-card-actions class="ghx-stepper__action-wrapper pb-0 px-0">
                    <v-spacer></v-spacer>
                    <v-btn
                      @click="getResult"
                      large
                      rounded
                      depressed
                      color="undefined"
                      height="50"
                      class="ghx-btn ghx-btn--primary ghx-btn--primary--text col-xs-hide col-sm-hide"
                      :class="[`ghx-btn--${btnStyle}`]"
                    >
                      Get My Results
                    </v-btn>
                    <v-btn
                      @click="getResult"
                      rounded
                      depressed
                      color="undefined"
                      height="50"
                      class="ghx-btn ghx-btn--primary ghx-btn--primary--has-icon col-md-hide col-lg-hide"
                      :class="[`ghx-btn--${btnStyle}`]"
                    >
                      <!-- <v-icon right size="30">mdi-arrow-right</v-icon> -->
                      <SvgIcons name="continue"></SvgIcons>
                    </v-btn>
                  </v-card-actions>
                </v-col>
              </v-card>
            </v-stepper-items>
          </v-stepper>

          <v-stepper
            v-if="saveForLater"
            class="ghx-stepper elevation-0"
            alt-labels
            v-show="isShowSaveFormPage"
          >
            <v-stepper-items>
              <div
                class="ghx-stepper-content"
                :class="[
                  saveForLaterStyle
                    ? `ghx-stepper-content--${saveForLaterStyle}`
                    : 'ghx-stepper-content--style-1',
                  footerStyle
                    ? `ghx-stepper-content--footer-${footerStyle}`
                    : 'ghx-stepper-content--footer-style-1',
                ]"
              >
                <div class="v-stepper__wrapper">
                  <div class="py-8">
                    <SaveAndExit
                      :isShow="isShowSaveFormPage"
                      @on-reset="$emit('on-reset')"
                      @on-item-selected="onFilledForm"
                      @on-item-emptied="onEmptyForm()"
                    />
                  </div>
                </div>
              </div>

              <v-card
                v-if="el"
                class="ghx-stepper__action ghx-glass-background pa-0 d-flex"
                :class="[`ghx-stepper__action--${footerStyle}`]"
              >
                <v-col col="12" class="ghx-action-btn-container d-flex pa-0">
                  <v-card-actions
                    class="ghx-stepper__action-wrapper pb-0 pt-4 px-0"
                  >
                    <template v-if="isExitClicked">
                      <v-btn
                        v-if="
                          isSaveForLaterStyle1 && hideSaveSpotBtnInFinalPage
                        "
                        @click="onContinueSteps"
                        large
                        rounded
                        depressed
                        text
                        :ripple="false"
                        min-width="0"
                        color="undefined"
                        class="ghx-btn ghx-btn--secondary ghx-btn--secondary--has-icon ghx-btn--secondary--text ml-0"
                        :class="[`ghx-btn--${btnStyle}`]"
                      >
                        Cancel
                      </v-btn>

                      <v-spacer></v-spacer>

                      <v-btn
                        v-if="
                          isSaveForLaterStyle2 && hideSaveSpotBtnInFinalPage
                        "
                        @click="onContinueSteps"
                        large
                        rounded
                        depressed
                        text
                        :ripple="false"
                        color="undefined"
                        class="ghx-btn ghx-btn--secondary ghx-btn--secondary--has-icon ghx-btn--animate-left mr-4 col-xs-hide col-sm-hide"
                        :class="[`ghx-btn--${btnStyle}`]"
                      >
                        <!-- <v-icon right >mdi-arrow-left</v-icon> -->
                        <SvgIcons name="back"></SvgIcons>
                        Back
                      </v-btn>

                      <v-btn
                        @click="onClickedExit"
                        :loading="saveSpotBtnLoading"
                        large
                        rounded
                        depressed
                        color="undefined"
                        height="50"
                        class="ghx-btn ghx-btn--primary ghx-btn--primary--text"
                        :class="[`ghx-btn--${btnStyle}`]"
                      >
                        Exit
                      </v-btn>
                    </template>

                    <template v-else>
                      <v-btn
                        @click="onContinueSteps"
                        large
                        rounded
                        depressed
                        text
                        :ripple="false"
                        min-width="0"
                        color="undefined"
                        class="ghx-btn ghx-btn--secondary ghx-btn--secondary--has-icon ghx-btn--secondary--text ml-0"
                        :class="[`ghx-btn--${btnStyle}`]"
                      >
                        Cancel
                      </v-btn>

                      <v-spacer></v-spacer>

                      <v-btn
                        @click="onClickSaveSpot"
                        :loading="saveSpotBtnLoading"
                        :disabled="!saveSpotForm"
                        large
                        rounded
                        depressed
                        color="undefined"
                        height="50"
                        class="ghx-btn ghx-btn--primary ghx-btn--primary--text col-xs-hide col-sm-hide"
                        :class="[`ghx-btn--${btnStyle}`]"
                      >
                        {{
                          currentPage?.btnSaveSpot || "Save My Spot And Exit"
                        }}
                      </v-btn>

                      <v-btn
                        @click="onClickSaveSpot"
                        :loading="saveSpotBtnLoading"
                        :disabled="!saveSpotForm"
                        rounded
                        depressed
                        color="undefined"
                        height="50"
                        class="ghx-btn ghx-btn--primary ghx-btn--primary--has-icon col-md-hide col-lg-hide"
                        :class="[`ghx-btn--${btnStyle}`,
                        {'ghx-btn--mobile': mobileAndTabletCheck}]"
                      >
                        <v-icon right>mdi-arrow-right</v-icon>
                        <!-- <SvgIcons name="continue"></SvgIcons> -->
                      </v-btn>
                    </template>
                  </v-card-actions>
                </v-col>
              </v-card>
            </v-stepper-items>
          </v-stepper>
        </v-col>
      </v-row>
    </v-card>
  </v-card>
</template>

<script>
import { ref, watch, computed, onMounted, nextTick } from "vue";
import {
  PERSONAL,
  GENERAL,
  DEFAULT_PERSONAL_QUESTIONS,
  DEFAULT_GENERAL_QUESTIONS,
} from "@/constants/questions";
import PageRadio from "./Pages/PageRadio";
import PageDate from "./Pages/PageDate";
import PageInit from "./Pages/PageInit";
import PageScreener from "./Pages/PageScreener";
import PageResult from "./Pages/PageResult";
import PageInstruction from "./Pages/PageInstruction";
import PageForm from "./Pages/PageForm";
import PageVideo from "./Pages/PageVideo";
import SaveAndExit from "./SaveAndExit";
import SvgIcons from "./Svg/SvgIcons";
//import PageLoader from "./Common/PageLoader";
import DisplayResult from "./DisplayResult.vue";
import Audigy from "@/models/Audigy";
import { questionnaire } from "@/store/main";
import Util from "@/helpers/Util";

export default {
  setup() {
    const store = questionnaire();
    const el = ref(0);
    const animationStartAt = ref(0);
    const activity = ref(0);
    const lastPage = ref(0);
    const selectedItem = ref(null);
    const saveSpotForm = ref(null);
    const forceContinue = ref(false);
    const excludedBtns = ref(["info1", "info2", "info3", "init", "video"]); //ref([8, 9, 10, 15, 19])
    const selectedFlow = ref(0);
    const isShowSaveForm = ref(false);
    const skipInitExperiment = ref(false);
    const isExitClicked = ref(false);
    const isVideoPlayed = ref(false);
    const handleVideoStart = () => {
      isVideoPlayed.value = true;
    };
    const config_object = computed(() => store.config);
    const config = computed(() => config_object.value.config);
    const theme = computed(() => config_object.value.theme);
    const saveForLater = computed(() => config.value.saveForLater);
    const btnStyle = computed(() => theme.value.btnStyle || "style-1");
    const headerStyle = computed(() => theme.value.headerStyle || "style-1");
    const footerStyle = computed(() => theme.value.footerStyle || "style-1");
    const saveForLaterStyle = computed(
      () => saveForLater.value.style || "style-1"
    );
    const isSaveForLaterStyle1 = computed(
      () => saveForLaterStyle.value === "style-1"
    );
    const isSaveForLaterStyle2 = computed(
      () => saveForLaterStyle.value === "style-2"
    );
    const flowSelector = computed(() => {
      return config.value?.activities[selectedFlow.value].pages;
    });
    const currentPage = computed(() => flowSelector.value[el.value - 1]);
    const progressValue = computed(() => {
      return (el.value / flowSelector.value.length) * 100;
    });
    const isExcludedBtn = computed(
      () =>
        excludedBtns.value?.indexOf(
          flowSelector.value?.[el.value - 1]?.form?.type
        ) < 0
    );
    const isStyleOne = computed(() => currentPage.value?.style === "style-1");
    const isStyleTwo = computed(() => currentPage.value?.style === "style-2");
    const hasScreeners = computed(() => flowSelector.value.find(item => item.form.type === 'screener'))
    const mobileAndTabletCheck = computed(() => Util.mobileAndTabletCheck())

    watch(el, async (currentValue) => {
      forceContinue.value = !isExcludedBtn.value;

      onSetStepIndicatorWidth(currentValue);
    });

    const concatText = (args) => {
      var str = "";
      args.forEach((item) => {
        str = `${str}${item}`;
      });

      return str;
    };

    // Methods
    const onSelectItem = (data) => {
      selectedItem.value = data;
      if (data?.resource?.form?.meta === "type") {
        selectedFlow.value = data?.resource?.form?.options.findIndex(
          (item) => item.label === data.selected
        );
      }
    };

    const onEmptyItem = (type) => {
      selectedItem.value = null;
    };

    const onEmptyForm = () => {
      console.log('onEmptyForm')
      saveSpotForm.value = null;
    };

    const onFilledForm = (data) => {
      saveSpotForm.value = data;
    };

    const onContinue = () => {
      var selectedItemValue = selectedItem.value;
      var meta =
        selectedItemValue.resource?.form?.meta === "final"
          ? selectedItemValue.resource?.form?.meta
          : concatText([
              selectedItemValue.resource.form.meta,
              selectedItemValue.resource.form.hz
                ? selectedItemValue.resource.form.hz
                : "",
            ]);
      skipInitExperiment.value = false;

      if (meta === "date_of_birth") {
        var prevVal = store.getUserResponse[meta];

        skipInitExperiment.value = selectedItemValue.selected === prevVal;
      }

      store.addUserResponse({
        field: meta,
        value: selectedItemValue.selected,
      });
    };

    const onContinueScreener = () => {
      if (!forceContinue.value) {
        store.addUserResponse({
          field: concatText([
            selectedItem.value.resource.form.meta,
            selectedItem.value.resource.form.hz
              ? selectedItem.value.resource.form.hz
              : "",
          ]),
          value: selectedItem.value.selected,
        });
      }
    };

    const onPrevious = () => {
      const ga_btn_back = currentPage.value?.googleAnalytics?.find(
        (item) => item.type === "btnBack"
      );

      Util.generateGoogleAnalytics(ga_btn_back.value, currentPage.value);

      el.value--;

      var prevItem = flowSelector.value.find((item) => item.order === el.value);

      selectedItem.value = {
        selected: {
          label:
            store?.getUserResponse[
              prevItem?.form?.meta + prevItem?.form?.hz
                ? prevItem?.form?.hz
                : ""
            ],
        },
        resource: prevItem,
      };
    };

    const onSaveSpotForm = (isExit = true) => {
      isShowSaveForm.value = true;
      isExitClicked.value = isExit;
      let ga_btn;

      if (!isExit) {
        ga_btn = saveForLater.value?.googleAnalytics?.find(
          (item) => item.type === "btnSaveSpot"
        );

        Util.generateGoogleAnalytics(ga_btn.value, saveForLater.value);
      }
    };

    const onContinueSteps = () => {
      // const ga_btn_cancel = saveForLater.value?.googleAnalytics?.find(
      //   (item) => item.type === "btnCancel"
      // );

      // Util.generateGoogleAnalytics(ga_btn_cancel.value, saveForLater.value);

      isShowSaveForm.value = false;
      isExitClicked.value = false;
    };

    const onInitialization = (data) => {
      selectedFlow.value = data === "Loved One" ? GENERAL : PERSONAL;
    };

    const onGetTotalItems = (key, value, items) => {
      return items.filter((item) => item[key] === value);
    };

    const onIntStepIndicator = async (data) => {
      await nextTick();

      var steps = document.querySelectorAll(".ghx-stepper-step");

      steps.forEach((stp, i) => {
        var isLastLine = i === steps.length - 1;
        var divider = stp.querySelector(".ghx-divider");
        var nextIndex = isLastLine ? 0 : i + 1;
        var nextSibling = steps[nextIndex];

        var startLine = stp.clientWidth / 2 + stp.offsetLeft;
        var endLine = nextSibling.clientWidth / 2 + nextSibling.offsetLeft;

        var lineWidth = Math.abs(endLine - startLine);

        if (isLastLine) divider.style.width = lineWidth + "px";
        else
          divider.setAttribute(
            "data-step-width",
            lineWidth / parseInt(divider.getAttribute("data-items"))
          );
      });
    };

    const onSetStepIndicatorWidth = async (currentValue) => {
      if (flowSelector.value.length === currentValue - 1) return;
      var data = flowSelector.value.find((item) => item.order === currentValue);
      var step = data.step;
      var stepItems = onGetTotalItems("step", step, flowSelector.value);
      var orderIndex = stepItems.findIndex(
        (item) => item.order === currentValue
      );
      
      if (step > 1 && !orderIndex) {
        var prevDivider = document.querySelector(
          ".ghx-stepper-step" + (step - 1) + " .ghx-divider"
        );
        
        prevDivider.style.width =
          parseInt(prevDivider.getAttribute("data-step-width")) *
            parseInt(prevDivider.getAttribute("data-items")) +
          "px";
      }

      var stepElems = document.querySelectorAll(".ghx-stepper-step");

      stepElems.forEach((stepElem) => {
        var divider = stepElem.querySelector(".ghx-divider");
        divider.classList.remove("active");

        if (stepElem.classList.contains("ghx-stepper-step" + step)) {
          divider.classList.add("active");
          divider.style.width = parseInt(divider.getAttribute("data-step-width")) * orderIndex + "px";
        }
      });
    };

    const onResize = async () => {
      onIntStepIndicator(flowSelector.value);
      onSetStepIndicatorWidth(el.value);
    };

    return {
      config_object,
      config,
      theme,
      saveForLater,
      btnStyle,
      headerStyle,
      footerStyle,
      saveForLaterStyle,
      isSaveForLaterStyle1,
      isSaveForLaterStyle2,
      forceContinue,
      store,
      el,
      activity,
      lastPage,
      selectedItem,
      saveSpotForm,
      currentPage,
      flowSelector,
      isExcludedBtn,
      isStyleOne,
      isStyleTwo,
      hasScreeners,
      progressValue,
      animationStartAt,
      onSelectItem,
      onEmptyItem,
      onEmptyForm,
      onFilledForm,
      onContinue,
      onContinueScreener,
      onPrevious,
      //onSaveSpot,
      onSaveSpotForm,
      onContinueSteps,
      onInitialization,
      onGetTotalItems,
      onIntStepIndicator,
      onSetStepIndicatorWidth,
      onResize,
      excludedBtns,
      selectedFlow,
      isShowSaveForm,
      skipInitExperiment,
      isExitClicked,
      isVideoPlayed,
      handleVideoStart,
      mobileAndTabletCheck
    };
  },

  props: {
    defaultLastPage: {
      type: Number,
      default: 1,
    },
  },

  data() {
    return {
      finalResult: null,
      buttonLoading: false,
      saveSpotBtnLoading: false,
      showInfo: false,
      isShowLoader: false,
    };
  },

  components: {
    //PageLoader,
    PageRadio,
    PageDate,
    PageInit,
    PageScreener,
    PageResult,
    PageVideo,
    PageInstruction,
    PageForm,
    SaveAndExit,
    SvgIcons,
  },

  computed: {
    personalQuestions() {
      return DEFAULT_PERSONAL_QUESTIONS;
    },
    generalQuestions() {
      return DEFAULT_GENERAL_QUESTIONS;
    },
    general() {
      return GENERAL;
    },
    personal() {
      return PERSONAL;
    },

    isShowSaveFormPage() {
      return this.isShowSaveForm;
    },

    selectedQuestion() {
      if (this.selectedFlow === this.personal) return this.personalQuestions;
      else return this.generalQuestions;
    },

    isFinalPage() {
      //var activity = this.config?.activities?.[this.activity].pages.find(item => item.order === this.el);
      return this.currentPage?.form?.meta === "final"; //activity?.form?.meta === 'final'
    },

    isResultPage() {
      return this.currentPage?.form?.meta === "result"; //activity?.form?.meta === 'final'
    },

    isShowFinalPage() {
      const params = Util.getUrlParams();

      return params?.testing === "true";
    },

    getUrlParams() {
      return Util.getUrlParams();
    },

    hasResultPage() {
      return this.flowSelector.find((item) => item.form.type === "end");
    },

    hideSaveSpotBtnInFinalPage() {
      return (
        !(this.isFinalPage && (this.currentPage?.form?.type === "form" || this.currentPage?.form?.type === "end")) &&
        !this.isResultPage &&
        !this.isShowLoader
      );
    },
  },

  watch: {
    flowSelector(currentValue) {
      this.onIntStepIndicator(currentValue);
    },

    async el(val) {
      await Util.sleep(100);
      this.animationStartAt = val;

      /**
       *  Pause or reset the video when navigating away from video page
       */
      if (this.$refs["videoStep"] && this.isVideoPlayed) {
        if (this.selectedFlow === 0) {
          /**
           *  Video page for "Me" option
           */
          if (val < 20) {
            this.$refs["videoStep"][0]?.player.pause();
          } else if (val == 20) {
            this.$refs["videoStep"][0]?.player.play();
          } else if (val > 20) {
            this.$refs["videoStep"][0]?.player.setCurrentTime(0);
            this.$refs["videoStep"][0]?.player.pause();
          }
        } else if (this.selectedFlow === 1) {
          /**
           *  Video page for "Loved One" option
           */
          if (val < 7) {
            this.$refs["videoStep"][0]?.player.pause();
          } else if (val == 7) {
            this.$refs["videoStep"][0]?.player.play();
          } else if (val > 7) {
            this.$refs["videoStep"][0]?.player.setCurrentTime(0);
            this.$refs["videoStep"][0]?.player.pause();
          }
        }
      }
    },
  },

  beforeDestroy() {
    window.removeEventListener("resize", this.onResize, { passive: true });
    window.removeEventListener("keypress", this.keypress);
  },

  mounted() {
    window.addEventListener("resize", this.onResize, { passive: true });
    this.el = parseInt(this.defaultLastPage);
    this.lastPage = this.el;
    this.onIntStepIndicator(this.flowSelector);
    window.addEventListener("keypress", this.keypress);
  },

  methods: {
    is_empty(val) {
      return Util.is_empty(val);
    },

    async onClickContinue(skip = false) {
      this.buttonLoading = true;
      this.$emit("on-show-loader");

      if (skip) this.onContinueScreener();
      else this.onContinue();

      const ga_btn_continue = this.currentPage?.googleAnalytics?.find(
        (item) => item.type === "btnContinue"
      );
      
      Util.generateGoogleAnalytics(
        ga_btn_continue.value,
        this.currentPage,
        this.selectedItem?.selected?.volume ?? this.selectedItem?.selected
      );

      if (this.currentPage?.form?.meta === "final" && this.hasScreeners) {
        this.isShowLoader = true;
        const screener =
          this.$refs[
            `screener${this.currentPage.order}${this.currentPage?.form?.hz}`
          ]?.[0];
        if (screener) {
          const sound = screener?.sound;

          clearTimeout(screener?.menuTimeout);

          if (sound) {
            sound.stop();
          }
        }

        await Audigy.saveExperimentData(
          "",
          this.selectedItem.resource.form.meta,
          this.selectedItem.resource?.form?.hz
            ? this.selectedItem.resource.form.hz
            : "",
          this.selectedItem.resource?.form?.ear
            ? this.selectedItem.resource.form.ear
            : ""
        );
      }

      if (this.isFinalPage) {
        this.finalResult = await this.saveMySpotAndLP(
          null,
          true
        );

        if (!this.isShowFinalPage) {
          this.getResult();
          return;
        }
      }

      this.el++;

      if (this.currentPage?.form?.meta === "result") {
        await Util.sleep(300);
        this.isShowLoader = false;
      }

      if (this.lastPage < this.el) this.lastPage++;

      this.selectedItem = null;
      this.buttonLoading = false;
      this.$emit("on-show-loader");
    },

    async onSaveSpot(id, link, isResultPage, finalResult = null) {
      if (!this.saveSpotForm && !isResultPage) return;

      var response = await Audigy.saveSpotForLater(
        !isResultPage
          ? this.saveSpotForm?.selected?.email
          : this.store?.getUserResponse?.final?.email,
        !isResultPage
          ? this.saveSpotForm?.selected?.first_name
          : this.store?.getUserResponse?.final?.first_name,
        id,
        this.flowSelector, //this.selectedQuestion,
        this.lastPage,
        this.selectedFlow,
        isResultPage,
        link,
        finalResult
      );
    },

    async saveMySpotAndLP(form, isResultPage = false) {
      var id = this.getUrlParams?.savedSpot || Audigy.generateWPId();
      var finalResult = await Audigy.saveFinalData(
        id,
        this.selectedQuestion,
        form,
        isResultPage ? "GetMyResults" : "SaveMySpot"
      );

      var link = "";
      
      if (isResultPage)
        link = window.location.origin + "/guided-hearing-experience/results?guid=" + id ;
      else
        link = window.location.origin + "?savedSpot=" + id;

      await this.onSaveSpot(id, link, isResultPage, finalResult);

      return finalResult;
    },

    async onClickSaveSpot() {
      this.$emit("on-show-loader");
      this.saveSpotBtnLoading = true;
      await this.saveMySpotAndLP(this.saveSpotForm?.selected);
      this.resetAndClose();
      this.saveSpotBtnLoading = false;
    },

    resetAndClose() {
      this.$emit("on-reset");
      this.$emit("on-close");
    },

    onClickedExit() {
      if (this.saveSpotForm) {
        this.onClickSaveSpot();
        return;
      }

      this.resetAndClose();
    },

    getResult() {
      const redirectOption = this.store?.ghxSettings?.ghx_redirect_option;

      switch (redirectOption) {
        case "Current Page":
          this.isExitClicked = true;
          this.onClickExit();
          break;
      
        case "Custom Page":
          window.location = this.store?.ghxSettings?.ghx_custom_url;
        break;
    
        default:
          var dataId = this.getUrlParams?.savedSpot || this.finalResult.data.id || "";
          var flow = "loved-one";

          if (this.selectedFlow === this.personal) flow = "for-me";
          
          window.location = `${window.location.origin}/guided-hearing-experience/results/${flow}?guid=${dataId}`;
          break;
      }
    },

    onClickExit() {
      if (this.isExitClicked) {
        const ga_btn_exit = this.saveForLater?.googleAnalytics?.find(
          (item) => item.type === "btnExit"
        );

        Util.generateGoogleAnalytics(ga_btn_exit.value, this.saveForLater);

        this.resetAndClose();
      } else {
        const ga_btn_exit = this.currentPage?.googleAnalytics?.find(
          (item) => item.type === "btnExit"
        );

        Util.generateGoogleAnalytics(ga_btn_exit.value, this.currentPage);

        this.onSaveSpotForm();
      }
    },

    keypress(e) {
      if (e.target.classList.contains("ghx-btn-play-song")) e.preventDefault();

      var modals = document.querySelectorAll(".menu-tick-" + this.el);
      modals.forEach((item) => {
        item.style.display = "none";
      });

      if (e.key === "Enter") this.onHitEnter();
    },

    onHitEnter() {
      var excludedBtn = this.isExcludedBtn;
      if (this.isShowSaveFormPage && this.saveSpotForm) this.onClickSaveSpot();
      else if (this.selectedItem || !excludedBtn)
        this.onClickContinue(!excludedBtn);
    },

    onHitEnterOnInput(e) {
      e.target.blur();
    },

    getVersion(type) {
      switch (type) {
        case "info1":
          return "1";
        case "info2":
          return "2";
        default:
          return "3";
      }
    },
  },
};
</script>
