<template>
  <div ref="container" class="ghx-app">
    <v-app>
      <v-main>
        <Audigy/>
      </v-main>
    </v-app>
  </div>
</template>

<script>
import { ref } from 'vue'
import Audigy from './components/Audigy';
//import Api from '@/models/Api'
import Util from '@/helpers/Util'
import _Audigy from '@/models/Audigy'
import { leadProcessor, questionnaire } from '@/store/main'

export default {
  name: 'App',
  
  setup() {
    const store_lp = leadProcessor()
    const store = questionnaire()

    const container = ref(null)
    
    return {
      store,
      store_lp,
      container
    }
  },

  components: {
    Audigy,
  },

  computed: {
    theme() {
      return this.store.config.theme
    }
  },

  created(){
    if (!this.theme.font)
      return

    var fontStyle = document.createElement('style');
    fontStyle.appendChild(document.createTextNode(`@import url('${this.theme.font}')`));

    document.head.appendChild(fontStyle);
  },
  
  async mounted() {
    if (!this.store_lp.getToken) {
      //var requestToken = await Api.requestToken();
      
      var requestToken = await _Audigy.getLpToken();
      if (requestToken?.data?.body) {
        var access_token = JSON.parse(requestToken?.data?.body);
        access_token = access_token?.access_token
        
        this.store_lp.setToken(access_token);

        await _Audigy.setGHXSettings();
      }
    }
    
    Util.setCssProperty(this.container, this.theme)
    window.getLpData();
  },
};
</script>

<style lang="scss">
    @import "@/assets/styles/style.scss";
    @import "//cdn.jsdelivr.net/npm/@mdi/font@6.x/css/materialdesignicons.min.css";
</style>