<template>
  <v-container class="ghx-stepper-body pa-0">
    <v-card flat class="ghx-glass-background pa-0">
      <v-row>
        <v-col cols="12">
          <v-card-text class="ghx-subtitle text-center pt-0 px-0 pb-4">
            <p
              class="ghx-stepper-content__question text--primary mb-0 pb-8"
              v-html="page.title"
            />
            <p class="text-subtitle-1 font-weight-light pb-7 mb-0">
              {{ page.message }}
            </p>
          </v-card-text>

          <div class="video-wrapper">
            <iframe
              :src="videoSrc"
              frameborder="0"
              allow="autoplay; fullscreen; picture-in-picture"
              allowfullscreen
              mozallowfullscreen
              webkitallowfullscreen
              id="video-player"
            ></iframe>
          </div>
        </v-col>
      </v-row>
    </v-card>
  </v-container>
</template>
<script setup>
import { ref, computed, defineEmits, onMounted, onBeforeUnmount } from "vue";
import Util from "@/helpers/Util";

const emit = defineEmits(["video-start"]);

const props = defineProps({
  isShow: {
    type: Boolean,
    default: false,
  },
  page: {
    type: Object,
    default: () => {},
  },
  currentOrder: {
    type: Number,
    default: 0,
  },
  disabled: {
    type: Boolean,
    default: false,
  },
});

const page = computed(() => props.page);
const videoSrc = computed(() => page.value.form.video);
const player = ref(null);
const init = () => {
  // add video script
  const jsElm = document.createElement("script");
  jsElm.type = "application/javascript";
  jsElm.src = "https://player.vimeo.com/api/player.js";
  jsElm.id = "vimeo-player-script";
  jsElm.onload = () => {
    const iframe = document.querySelector("#video-player");
    player.value = new Vimeo.Player(iframe);

    player.value.on("play", function () {
      emit("video-start");
    });
  };
  document.body.appendChild(jsElm);
};

onMounted(() => {
  init();
});

onBeforeUnmount(() => {
  // remove video script
  document.getElementById("vimeo-player-script")?.remove();
});
function onRestart() {
  const ga_btn_exit = page.value?.googleAnalytics?.find(
    (item) => item.type === "btnRestart"
  );

  Util.generateGoogleAnalytics(ga_btn_exit.value, page.value);
  emit("on-reset");
}

defineExpose({
  player,
});
</script>

<style scoped>
.video-wrapper {
  max-width: 500px;
  margin: auto;
  padding: 280px 0 0 0;
  position: relative;
}

@media only screen and (max-width: 550px) {
  .video-wrapper {
    width: 300px;
    margin: auto;
    padding: 170px 0 0 0;
    position: relative;
  }
}

.video-wrapper iframe {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  border-radius: 20px;
}
</style>
