<template>

	<ul class="list-group parent-list-group">
	    <li class="list-group-item list-item list-item-heading">
	    	<EditableListInput :list-label="list[0]" :list-id="0" @onInputChange="getInput($event)"/>
	    </li>
	    <ul class="list-group sub-list-group">
			<li class="list-group-item list-item">
				<EditableListInput :list-label="list[1]" :list-id="1" @onInputChange="getInput($event)" />
			</li>
			<li class="list-group-item list-item">
				<EditableListInput :list-label="list[2]" :list-id="2" @onInputChange="getInput($event)" />
			</li>
			<div class="options-form">
				<h2 class="options_title">Options</h2>
				<div class="question_container">
					<div class="row">
						<div class="col-sm-6">
							
							<div class="row">
								<div class="col-sm-3"></div>
								<div class="col-sm-9">
									<h2 class="options_header" style="">Selection Text</h2>
								</div>
							</div>
							<div class="form-group">
								<div class="row">
									<label for="staticEmail" class="col-sm-3 col-form-label">Unselected</label>
								</div>
							</div>
							<div class="form-group">
								<div class="row">
									<div class="col-sm-3">
										<label>Option 1</label>
									</div>
									<div class="col-sm-9">
										<input type="text" class="form-control-plaintext question_inputs" value="The default conditional text message goes here.">
									</div>
								</div>
							</div>
							<div class="form-group">
								<div class="row">
									<div class="col-sm-3">
										<label>Option 2</label>
									</div>
									<div class="col-sm-9">
										<input type="text" class="form-control-plaintext question_inputs" value="The default conditional text message goes here.">
									</div>
								</div>
							</div>
						</div>
						<div class="col-sm-6">
							<h2 class="options_header">Condition Message Text</h2>
							<div class="form-group">
								<div class="row">
									<div class="col-sm-12">
										<input type="text" class="form-control-plaintext question_inputs" value="The default conditional text message goes here.">
									</div>
								</div>
							</div>
							<div class="form-group">
								<div class="row">
									<div class="col-sm-12">
										<input type="text" class="form-control-plaintext question_inputs" value="The default conditional text message goes here.">
									</div>
								</div>
							</div>
							<div class="form-group">
								<div class="row">
									<div class="col-sm-12">
										<input type="text" class="form-control-plaintext question_inputs" value="The default conditional text message goes here.">
									</div>
								</div>
							</div>
						</div>
					</div>
				</div>
			</div>
		</ul>
	</ul>
</template>


<script>

	import EditableListInput from "../components/EditableInput.vue";

	export default {
		components: {
		   EditableListInput
		},
		data() {
		    return {
		      list: [
		      	"First, let's get to know.",
		      	"Are you here for you or a loved one?",
		      	"(Make a selection and continue)"
		      ],
		    }
		},
		methods: {
		    getInput(e) {
		        var el = jQuery(e).parent().parent().find('.list-item-label');
		      

		      if(el.attr('contentEditable')){
		      	el.removeAttr('contentEditable');

		      	this.list[el.attr('list-id')] = el.text();

		      }else{
		      	
		      	el.attr('contentEditable', true);
		      }

		    },
		},
	}
</script>