<template>
  <v-container class="ghx-stepper-body pa-0">
    <v-row>
      <v-col 
        cols="12" 
        class="col-lg-12 col-sm-12"
        >
        <v-card flat class="ghx-glass-background pa-0">
          <v-card-text class="text-center pa-0">
            <p
              v-if="page.title"
              class="ghx-stepper-content__question"
              v-html="page.title"
              />
            <p
              v-if="page.secondInstuction"
              class="ghx-stepper-content__instruction-2 text-center"
              v-html="page.secondInstuction"
              />
          </v-card-text>
          <v-card-text class="text-center ghx-stepper-body__content pb-0">
            <v-item-group
              :key="details.meta + details.type"
              active-class="primary"
              >
              <div class="ghx-question-selector-wrapper">
                <v-card
                  class="ghx-glass-background ghx-date-picker"
                  flat
                  width="200"
                  >
                  <v-text-field
                    v-model="year"
                    class="ghx-form-input mt-2"
                    label="Year"
                    :rules="[rules.year]"
                    outlined
                    @keyup.enter="$emit('on-hit-enter')"></v-text-field>
                </v-card>
              </div>
            </v-item-group>
          </v-card-text>
        </v-card>
      </v-col>
    </v-row>
  </v-container>
</template>
  
<script setup>
import {
  ref,
  watch,
  computed,
  defineEmits,
  onMounted,
  onBeforeUnmount
} from 'vue';
import { questionnaire } from '@/store/main'

const emit = defineEmits(['initialized', 'on-item-selected', 'on-item-emptied'])

const props = defineProps({
  page: {
    type: Object,
    default: () => {}
  },
  currentOrder: {
    type: Number,
    default: () => 0
  }
})

const store = questionnaire()

const YEAR_NUMBER_LENGTH = 4;
const hasSelectedItem = ref(null)
const selectedItem = ref(null)
const year = ref('')
const rules = ref({
  year: value => {
    const x = value.replace(/\D/g, '').match(/(\d{0,4})/);
    const d = new Date();
    year.value = x[1];
    
    return year.value.length === YEAR_NUMBER_LENGTH && year.value <= d.getFullYear() && year.value >= 1900 ? true : 'Invalid Year.'
  }
})
const skip = ref(false)

const page = computed(() => props.page)
const currentOrder = computed(() => props.currentOrder)
const details = computed(() => page.value.form)

onBeforeUnmount(() => {
  skip.value = true
})

watch(selectedItem, (val) => {
  if (val) {
    const value = details.value.type === 'date' ? year.value: val;
    const resource = {
      selected: value,
      resource: page.value
    };

    emit('on-item-selected', resource)
    
    store.setSelectedFlow({key: details.value.meta, value: value});
  } else
      emit('on-item-emptied')
}, {deep: true})

watch(year, (val) => {
  const d = new Date();
  selectedItem.value = val.length === YEAR_NUMBER_LENGTH && val <= d.getFullYear() && val >= 1900 ? val: false;
})

watch(currentOrder, (val) => {
  if (val === page.value.order && selectedItem.value) {
    const resource = {
      selected: selectedItem.value,
      resource: page.value
    };
    
    emit('on-item-selected', resource)
  }
})

onMounted(() => {
  hasSelectedItem.value = store?.getSelectedFlow?.[details.value.meta] || store?.getUserResponse?.[details.value.meta] || null;
    
  if (hasSelectedItem.value)
    year.value = hasSelectedItem.value || null
})
</script>

<style lang="scss" scoped>

</style>