<template>
  <v-container class="ghx-stepper-body pa-0">
    <v-row>
      <v-col cols="12" class="col-lg-12 col-sm-12">
        <v-card flat class="ghx-glass-background pa-0">
          <v-card-text class="text-center pa-0">
            <p
              v-if="page.title"
              class="ghx-stepper-content__question"
              v-html="page.title"
            />
            <p
              v-if="page.secondInstuction"
              class="ghx-stepper-content__instruction-2 text-center"
              v-html="page.secondInstuction"
            />
          </v-card-text>
          <v-card-text class="text-center ghx-stepper-body__content pb-0">
            <v-item-group
              :key="details.meta"
              :multiple="details.multiSelect ? true : false"
              active-class="primary"
              :value="
                hasSelectedItem
                  ? hasSelectedItem
                  : details.multiSelect
                  ? []
                  : ''
              "
            >
              <div class="ghx-question-selector-wrapper">
                <div
                  v-for="(item, nkey) in page?.form.options"
                  :key="nkey"
                  cols="12"
                  md="4"
                  class="ghx-question-selector"
                >
                  <v-item v-slot="{ active, toggle }" :value="item.label">
                    <v-card
                      class="ghx-question-selector-item d-flex align-center ma-1 py-0 px-6"
                      :class="{ active: active }"
                      :size="details?.button_size"
                      :ripple="false"
                      @click="
                        onSelectedItem(item.label, active, item.reset);
                        toggle();
                      "
                    >
                      <v-scroll-y-transition>
                        <div class="ghx-option-item flex-grow-1 text-center">
                          <v-card-text
                            v-if="item.icon"
                            class="px-0 pt-0 d-flex justify-center"
                          >
                            <v-img
                              :src="item.icon"
                              height="48"
                              width="48"
                              max-width="48"
                            >
                            </v-img>
                          </v-card-text>
                          {{ item.label }}
                        </div>
                      </v-scroll-y-transition>
                    </v-card>
                  </v-item>
                </div>
              </div>
            </v-item-group>
          </v-card-text>
        </v-card>
      </v-col>
    </v-row>
  </v-container>
</template>
<script setup>
import { ref, watch, computed, defineEmits, onBeforeUnmount, onMounted } from "vue";
import { questionnaire } from "@/store/main";
import Util from '@/helpers/Util'

const emit = defineEmits(["on-item-selected", "on-item-emptied"]);

const props = defineProps({
  page: {
    type: Object,
    default: () => null,
  },
  currentOrder: {
    type: Number,
    default: () => 0,
  },
});

const store = questionnaire();

const hasSelectedItem = ref(null);
const multiSelectedItem = ref([]);
const selectedItem = ref(null);
const skip = ref(false);

const page = computed(() => props.page);
const details = computed(() => page.value.form);
const currentOrder = computed(() => props.currentOrder);

onBeforeUnmount(() => {
  skip.value = true;
});

onMounted(() => {
  const temp = store?.getSelectedFlow?.[details.value.meta] || store?.getUserResponse?.[details.value.meta] || null;
  hasSelectedItem.value = Util.filterSelectedDefaultValue(temp, details.value);


  if (hasSelectedItem.value)
    selectedItem.value = hasSelectedItem.value;
})

watch(
  selectedItem,
  (val) => {
    if (val) {
      const resource = {
        selected: val,
        resource: props.page,
      };

      emit("on-item-selected", resource);

      store.setSelectedFlow({ key: details.value.meta, value: val });
    } else emit("on-item-emptied");
  },
  { deep: true }
);

watch(currentOrder, (val) => {
  if (val === page.value.order && selectedItem.value) {
    var resource = {
      selected: selectedItem.value,
      resource: page.value,
    };

    emit("on-item-selected", resource);
  }
});

function onSelectedItem(data, isActive, reset) {
  if (reset) {
    if (details.value.multiSelect) {
      if (isActive) {
        const index = multiSelectedItem.value.findIndex((val) => val === data);
        multiSelectedItem.value.splice(index, 1);
      } else {
        multiSelectedItem.value = [];
        multiSelectedItem.value.push(data);
      }

      hasSelectedItem.value = multiSelectedItem.value;
    } else hasSelectedItem.value = isActive ? null : data;

    if (!isActive) {
      selectedItem.value = null;
    }

    selectedItem.value = hasSelectedItem.value.length
      ? hasSelectedItem.value
      : null;

    return;
  }

  if (details.value.multiSelect) {
    if (isActive) {
      var index = multiSelectedItem.value.findIndex((val) => val === data);
      multiSelectedItem.value.splice(index, 1);
    } else multiSelectedItem.value.push(data);

    selectedItem.value = multiSelectedItem.value.length
      ? multiSelectedItem.value
      : null;
  } else selectedItem.value = isActive ? null : data;

  if (!reset) {
    var index = multiSelectedItem.value.findIndex(
      (val) => val === "None of the Above"
    );

    if (index > -1) {
      if (details.value.multiSelect) {
        multiSelectedItem.value.splice(index, 1);
        hasSelectedItem.value = multiSelectedItem.value;
      }

      hasSelectedItem.value = selectedItem.value;
      selectedItem.value = null;
      selectedItem.value = hasSelectedItem.value;
    }
  }
}
</script>

<style lang="scss" scoped></style>
