<template>
  <v-container 
    class="ghx-stepper-body ghx-result-page pa-0"
    :class="[{'ghx-result-page--active': animationStarted}]"
    >
    <v-row>
      <v-col :cols="12">
        <v-card flat class="ghx-glass-background pa-0">
          <v-card-text class="text-center pa-0">
            <p
              v-if="page.title"
              class="ghx-stepper-content__question"
              v-html="page.title"
              />
            <p
              v-if="page.secondInstuction"
              class="ghx-stepper-content__instruction-2 text-center"
              v-html="page.secondInstuction"
              />
          </v-card-text>
          <v-card-text class="text-center pt-0 ghx-stepper-body__content ghx-screening">
            <div class="ghx-screening__left">
              <h2 class="ghx-screening__title">Left Ear</h2>

              <h3 class="ghx-screening__desc">Indication of hearing loss</h3>

              <div  class="ghx-screening__result">
                <div class="ghx-screening__result__wrapper">
                  <v-progress-linear 
                    :value="leftResultValue" 
                    height="56" 
                    class="ghx-screening__result__indicator"
                    color="undefined"
                    ></v-progress-linear>
                  <div class="ghx-screening__result__indicator__borders">
                    <span class="ghx-screening__result__indicator__borders__item"></span>
                    <span class="ghx-screening__result__indicator__borders__item"></span>
                    <span class="ghx-screening__result__indicator__borders__item"></span>
                    <span class="ghx-screening__result__indicator__borders__item"></span>
                  </div>
                </div>

                <div class="ghx-screening__result__label">
                  <h3>Normal</h3>
                  <h3>Profound</h3>
                </div>
              </div>
            </div>
            <div class="ghx-screening__right">
              <h2 class="ghx-screening__title">Right Ear</h2>

              <h3 class="ghx-screening__desc">Indication of hearing loss</h3>

              <div  class="ghx-screening__result">
                <div class="ghx-screening__result__wrapper">
                  <v-progress-linear 
                    :value="rightResultValue" 
                    height="56" 
                    class="ghx-screening__result__indicator"
                    color="undefined"
                    >
                  </v-progress-linear>
                  <div class="ghx-screening__result__indicator__borders">
                    <span class="ghx-screening__result__indicator__borders__item"></span>
                    <span class="ghx-screening__result__indicator__borders__item"></span>
                    <span class="ghx-screening__result__indicator__borders__item"></span>
                    <span class="ghx-screening__result__indicator__borders__item"></span>
                  </div>
                </div>
                <div class="ghx-screening__result__label">
                  <h3>Normal</h3>
                  <h3>Profound</h3>
                </div>
              </div>
            </div>
          </v-card-text>

          <v-card-text class="text-center ghx-stepper-body__content ghx-booking">
            <div class="ghx-booking__bg"></div>
            <div class="ghx-booking__wrapper">
              <div class="ghx-booking__content">
                <h2 class="ghx-booking__title">Book a Free Appointment</h2>
                <h2 class="ghx-booking__quote">Take the Next Step</h2>
                <p class="ghx-booking__description">Living with hearing loss doesn’t need to feel difficult or overwhelming. With hearing aids, you can continue to live life to the fullest and hear what matters the most to you</p>
                
                <div class="ghx-booking__btns">
                  <v-btn
                    @click="onClickAppointment"
                    large rounded depressed
                    color="undefined"
                    height="50"
                    class="ghx-btn ghx-btn--primary"
                    :class="[`ghx-btn--${btnStyle}`]"
                    >
                    Book an Appointment
                  </v-btn>

                  <v-btn
                    @click="onClickStartChat"
                    large rounded depressed
                    color="undefined"
                    height="50"
                    class="ghx-btn ghx-btn--secondary"
                    :class="[`ghx-btn--${btnStyle}`]"
                    >
                    Start a Chat
                  </v-btn>
                </div>
              </div>
            </div>
          </v-card-text>

          <v-card-text class="text-center ghx-stepper-body__content ghx-assessment">
            <div class="ghx-assessment__image"></div>
            <div class="ghx-assessment__content">
              <h2 class="ghx-assessment__title">
                Comprehensive Hearing Assesssment
              </h2>
              <p class="ghx-assessment__description">
                Untreated hearing loss makes communicating with friends and loved ones difficult and can lead to isolation and other serious health risks.
              </p>
              <p class="ghx-assessment__description">
                If you’re experiencing hearing loss, don’t wait to seek treatment. Visit your local Beltone office for a free hearing assessment that measures your hearing loss and helps your hearing care professional determine if hearing aids are a good fit for you.
              </p>

              <v-btn
                @click="onClickOfficeVisit"
                large rounded depressed
                color="undefined"
                height="50"
                class="ghx-btn ghx-btn--primary"
                :class="[`ghx-btn--${btnStyle}`]"
                >
                What to Expect at Your Office Visit
              </v-btn>
            </div>
          </v-card-text>
        </v-card>
      </v-col>
    </v-row>
  </v-container>
</template>

<script setup>
import {
  ref,
  computed,
  watch,
  defineProps
} from 'vue';
import { questionnaire } from '@/store/main'
import Util from '@/helpers/Util';

const props = defineProps({
  page: {
    type: Object,
    default: () => {}
  },
  theme: {
    type: Object,
    default: () => {}
  },
  currentOrder: {
    type: Number,
    default: () => 0
  }
})

const store = questionnaire()

const resultValues = ref([
  { result: 'NoImpairment', value: 7 },
  { result: 'SlightImpairment', value: 25 },
  { result: 'ModerateImpairment', value: 50 },
  { result: 'SevereImpairment', value: 75 },
  { result: 'ProfoundImpairment', value: 100 }
])
const animationStarted = ref(false)
const timeout = ref(null)

const page = computed(() => props.page)
const theme = computed(() => props.theme)
const currentOrder = computed(() => props.currentOrder)
const btnStyle = computed(() => theme.value.btnStyle || 'style-1')
const leftResultValue = computed(() => {
  const result = store.userApiData?.Left?.analyze

  if (!result)
    return 7

  return resultValues.value.find(item => item.result === result).value
})
const rightResultValue = computed(() => {
  const result = store.userApiData?.Right?.analyze

  if (!result)
      return 7
  
  return resultValues.value.find(item => item.result === result).value
})

watch(currentOrder, (val) => {
  if (val === page.value.order) {
      clearTimeout(timeout.value)

      timeout.value = setTimeout(_=> {
          animationStarted.value = true
          clearTimeout(timeout.value)
      }, 600)
  }
})

function onClickAppointment() {
  const ga_btn = page.value?.googleAnalytics?.find(item => item.type === 'btnAppointment')

  Util.generateGoogleAnalytics(ga_btn.value, page.value)
}

function onClickStartChat() {
  const ga_btn = page.value?.googleAnalytics?.find(item => item.type === 'btnStartChat')

  Util.generateGoogleAnalytics(ga_btn.value, page.value)
}

function onClickOfficeVisit() {
  const ga_btn = page.value?.googleAnalytics?.find(item => item.type === 'btnOfficeVisit')

  Util.generateGoogleAnalytics(ga_btn.value, page.value)
}
</script>