<template>
    <component
        :is="svgName"
    ></component>
</template>

<script setup>
import { computed, defineProps } from 'vue';
import SaveMySpotSvg from './SaveMySpotSvg'
import SaveMySportExitSvg from './SaveMySportExitSvg'
import CheckmarkSvg from './CheckmarkSvg'
import ContinueSvg from './ContinueSvg'
import BackSvg from './BackSvg'
import CloseSvg from './CloseSvg'

const props = defineProps({
    name: {
        type: String,
        default: ''
    }
})

const svgName = computed(() => {
    switch (props.name) {
        case 'bookmark':
            return SaveMySpotSvg
        case 'bookmark-exit':
            return SaveMySportExitSvg
        case 'checkmark':
            return CheckmarkSvg
        case 'continue':
            return ContinueSvg
        case 'back':
            return BackSvg
        case 'close':
            return CloseSvg
        default:
            return ''
    }
})
</script>