<template>
  <v-col col="12" class="pa-0 ghx-stepper-content">
    <v-col col="12">
      <v-row v-if="getFormData">
        <v-col col="12" class="col-12 pb-0"><label>Full Name:</label> <span>{{getFormData.first_name}} {{getFormData.last_name}}</span></v-col>
        <v-col col="12" class="col-12 pb-0"><label>Email Address:</label> <span>{{getFormData.email}}</span></v-col>
        <v-col col="4" class="col-4 pb-0"><lable>Phone Number:</lable> <span>{{getFormData.phone_number}}</span></v-col>
        <v-col col="8" class="col-8 pb-0"><label>Zip Code:</label> <span>{{getFormData.zip_code}}</span></v-col>
        <template v-for="(item, index) in getUserInfo">
          <v-col :key="'info' + index" col="12" class="col-12 pb-0"><label>{{item.label}}</label></v-col>
          <v-col :key="'info' + index" col="12" class="col-12 pb-0 pt-0">
            <template v-if="Array.isArray(item.value)">
              <v-col v-for="(val, index) in item.value" :key="val.label + index" col="12" class="col-12 pb-0 pt-0">
                <span>{{val.label}}</span>
              </v-col>
            </template>
            <v-col v-else col="12" class="col-12 pb-0 pt-0">
              <span>{{item.value}}</span>
            </v-col>
          </v-col>
        </template>
      </v-row>
    </v-col>
    <template v-for="(item, i) in getResultValue(store.getUserApiData)">
      <v-col v-if="item" col="12" :key="`ghx-api-result-${i}`" class=" pb-0">
        <v-row>
          <v-col col="12" class=" pb-0">
            <label>Ear: </label>
            <span>{{item.experiment.ear}}</span>
          </v-col>
        </v-row>
        <v-row>
          <v-col col="12" class=" pb-0">
            <label>Result: </label>
            <span>{{item.analyze}}</span>
          </v-col>
        </v-row>
        <v-row>
          <v-col col="12">
            <label>Trial Result: </label>
            <v-col col="12" v-for="(tr, index) in item.experiment.trialResults" :key="`tr${index}`">
              <v-row>
                <v-col col="12" class=" pb-0">
                  <label>Frequency:</label> <span>{{tr.tone.frequency}} hz</span>
                </v-col>
                
                <v-col col="12" class=" pb-0">
                  <label>Sound Intensity:</label> <span>{{tr.tone.intensityDbHL}} db</span>
                </v-col>
                
                <v-col col="12" class=" pb-0">
                  <label>Audible:</label> <span>{{tr.audible}}</span>
                </v-col>
             </v-row>
            </v-col>
          </v-col>
        </v-row>
     </v-col>
   </template>
  </v-col>
</template>

<script>
  
import { questionnaire } from '@/store/main'

export default {
  setup () {
    const store = questionnaire();
    
    return {
      store
    }
  },
  
  props: {
    selectedQuestion: {
      type: Array,
      default: () => []
    },
    selectedFlow: {
      type: Number,
      defult: 0
    }
  },
  
  computed: {
    getFormData() {
      return this.store?.getUserResponse?.final
    },
    
    getUserInfo() {
      var items = [];
      var userResponsed = this.store.getUserResponse;
      var filtered = this.selectedQuestion?.filter( item => (item.form.type === 'radio' && item.form.meta != 'type') || item.form.type === 'date');
      filtered.forEach( item => {
        var data = userResponsed[item.form.meta];
        
        if (data)
          items.push({label: item.form.type === 'date' ? 'Date of Birth:': item.text, value: data})
      })
      
      return items;
    }
  },
  
  methods: {
    getResultValue(apiData) {
      var data = [];
      
      Object.entries(apiData).forEach(([key, value]) => {
          data.push(value);
      })
      
      return data;
    }
  }
}
</script>