<template>
  <v-container 
      class="ghx-stepper-body pa-0"
      :class="['ghx-stepper-body__wrapper--v' + version]">
      <v-row>
          <v-col :cols="12">
              <v-card
                  v-if="page.form.type !== 'info2'"
                  flat class="ghx-glass-background pa-0">
                  <v-card-text class="text-center pa-0">
                      <p v-if="page.title" class="ghx-stepper-content__question" v-html="page.title"></p>
                      <p v-if="page.secondInstuction" class="ghx-stepper-content__instruction-2 text-center" v-html="page.secondInstuction"></p>
                  </v-card-text>
                  <v-card-text class="text-center pb-0 ghx-stepper-body__content">
                      <v-row class="mt-0 pb-0 justify-center">
                          <v-col cols="4"
                              v-for="(item, nkey) in details.options"
                              :key="nkey"
                              class="align-center ghx-instruction-box"
                              :class="[version === '3' ? 'px-3': 'px-2']">
                              <v-card 
                                  flat 
                                  class="pa-2 ghx-screening-info-box ghx-screening-info-box--v1"
                                  :class="[
                                      {'ghx-instruction-box__no-title': is_empty(item.label)}]">
                                  <v-card-text v-if="item.label" class="ghx-instruction-box__title">
                                      <p class="text-title text-left font-weight-bold ma-0 text-center">{{ item.label }}</p>
                                  </v-card-text>
                                  
                                  <v-card-text v-if="nkey === 0 && page.form.type === 'info1'" class="py-0 pt-2 pb-4 text-left">
                                      <template v-for="(content, index) in item.content">
                                        <p :key="`${content.label}${index}`" class="text-subtitle-1 font-weight-medium ma-0 pt-1">
                                          <span class="ghx-icon">
                                            <img width="16px" height="16px" class="mr-2" :src="content.icon"/>
                                          </span>
                                          {{ content.label }}
                                        </p>
                                      </template>
                                  </v-card-text>
                                  <template v-if="!(nkey === 0 && page.form.type === 'info1')">
                                    <v-card-text 
                                        v-if="Array.isArray(item.content)"
                                        class="pa-0 pb-4"
                                        :class="{hidden: nkey === 0 && page.form.type === 'info1'}">
                                        <template v-for="(content, index) in item.content">
                                            <v-list-item 
                                                v-if="!content.type || content.type !== 'button'"
                                                :key="`content-${index}`" 
                                                :class="{'ghx-instruction-box__content-wrapper--icon-only': !content.label && !content.description}">
                                                <v-list-item-avatar 
                                                    tile 
                                                    class="ghx-instruction-box__avatar"
                                                    :rounded="content.icon ? '0': 'sm'" 
                                                    :size="content.icon ? '30': '32'" 
                                                    :color="content.icon ? 'transparent': ''">
                                                    <span class="ghx-icon">
                                                        <img
                                                          v-if="content.icon"
                                                          width="30px"
                                                          height="30px"
                                                          class="force-size-30"
                                                          :src="content.icon"
                                                          />
                                                        <template v-else-if="content.order">{{ content.order }}</template>
                                                    </span>
                                                </v-list-item-avatar>

                                                <v-card-text v-if="content.label || content.description" class="pa-0 text-left">
                                                    <p v-if="content.label" class="text-subtitle-1 font-weight-medium ma-0 mb-2 pt-1" v-html="content.label"></p>
                                                    <v-list-item-subtitle v-if="content.description" class="ghx-instruction-box__desc text-left pa-0"  v-html="content.description">
                                                    </v-list-item-subtitle>
                                                </v-card-text>
                                            </v-list-item>

                                            <div :key="index" v-else class="px-4 pb-2 pb-sm-4">
                                                <v-btn 
                                                    large rounded depressed text
                                                    :ripple="false"
                                                    min-width="0"
                                                    color="undefined"
                                                    class="ghx-btn ghx-btn--primary ghx-btn ghx-btn--primary--has-icon ghx-btn--primary--text ghx-btn-play-song ghx-btn--animate-right" 
                                                    :class="[`ghx-btn--${btnStyle}`]"
                                                    @click="onPlayPause(require('@/assets/audio/hl_sound_check.mp3'))">
                                                    {{isPlayed ? content.alterLabel: content.label}}
                                                    <SvgIcons name="continue"></SvgIcons>
                                                </v-btn>
                                            </div>
                                        </template>
                                    </v-card-text>

                                    <v-card-text v-else class="py-0">
                                        <p class="text-title text-left text-center" v-html="item.content"></p>
                                    </v-card-text>
                                  </template>
                              </v-card>
                          </v-col>
                      </v-row>
                  </v-card-text>
              </v-card>
              <v-card
                v-else
                flat class="ghx-glass-background pa-0">
                <v-card-text class="text-left pa-0">
                  <p class="ghx-slide-text-header ghx-stepper-content__question text--primary text-center">{{ page.title }}</p>
                  <p class="text-body-1 text-center">
                    Background noises like air conditioning, fridges, and street traffic can interfere with the test.
                  </p>
                  <v-card-text class="text-center pb-0">
                    <v-row class="pt-6 pb-0 justify-center">
                      <v-col cols="4"
                        v-for="(i, key) in details.options"
                        :key="key"
                        class="align-center px-2 ghx-instruction-box">
                        <v-card flat class="pa-2 ghx-screening-info-box ghx-screening-info-box--v2">
                          <v-card-text>
                            <p class="text-body-1 text-left font-weight-bold text-center mb-0">
                              <span class="text-body-1 font-weight-bold pr-1">{{ key + 1 }}</span>
                              {{ i.content[0].description }}
                            </p>
                          </v-card-text>
                          <div class="px-4 pb-4" v-if="key === 1">
                            <button class="ghx-btn ghx-btn--primary ghx-btn-play-song" @click="onPlayPause(require('@/assets/audio/hl_sound_check.mp3'))">
                              {{isPlayed ? 'Stop Song': 'Play Song'}}
                              <v-icon right >mdi-arrow-right</v-icon>
                            </button>
                          </div>
                        </v-card>
                      </v-col>
                    </v-row>
                  </v-card-text>
                </v-card-text>
              </v-card>
          </v-col>
      </v-row>
  </v-container>
</template>

<script setup>
import { ref, computed, watch, defineProps, defineEmits, onMounted } from 'vue';
import { questionnaire } from '@/store/main'
import Util from '@/helpers/Util';
import SvgIcons from '../Svg/SvgIcons'

const emit = defineEmits(['on-save-spot'])

const props = defineProps({
  page: {
    type: Object,
    default: () => {}
  },
      
  currentOrder: {
      type: Number,
      default: 0
  },

  version: {
    type: String,
    default: '1'
  }
})

const store = questionnaire()

const audioExample = ref(null)
const isPlayed = ref(false)

const page = computed(() => props.page)
const version = computed(() => props.version)
const details = computed(() => page.value.form)
const config_object = computed(() => store.config)
const theme = computed(() => config_object.value.theme)
const btnStyle = computed(() => theme.value.btnStyle || 'style-1')
const currentOrder = computed(() => props.currentOrder)

watch(currentOrder, (val) => {
  if (val !== page.value.order && audioExample.value)
      audioControl(isPlayed.value = false);
})

onMounted(() => {
  let elem = document.querySelector('#save-spot')
  if (elem)
      elem.addEventListener('click', () => emit('on-save-spot') )
})

function is_empty(val) {
  return Util.is_empty(val)
}
 
function onPlayPause(url) {
  let ga_btn_play_song

  if (audioExample.value) {
      audioExample.value.stop()
      ga_btn_play_song = page.value?.googleAnalytics?.find(item => item.type === 'btnStopSong')
  }
  else
      ga_btn_play_song = page.value?.googleAnalytics?.find(item => item.type === 'btnPlaySong')

  Util.generateGoogleAnalytics(ga_btn_play_song.value, page.value)
      
  isPlayed.value = !isPlayed.value
  
  audioExample.value = new Howl({
      src: url,
      html5: true,
      volume: 0.1
  })
  
  audioControl(isPlayed.value);
}
    
function audioControl(isPlayed) {
  if (!audioExample.value)
      return

  if (isPlayed)
      audioExample.value.play()
  else 
      audioExample.value.stop()
}
</script>