<template>
  <v-container class="ghx-stepper-body pa-0">
    <v-row>
      <v-col cols="12" class="pb-0 pr-2">
        <v-card flat class="ghx-glass-background pa-0">
          <v-card-text class="text-center pa-0 pb-4">
            <p
              class="ghx-stepper-content__question text--primary"
              v-html="pageDetails.title"
            ></p>
          </v-card-text>
          <v-card-text class="pa-0 ghx-form">
            <v-row class="ma-0">
              <v-col cols="12" lg="6" md="6">
                <p class="text-subtitle-1">
                  <span v-html="pageDetails.message"></span
                  ><a href="#" @click="onRestart">Restart</a>.
                </p>
              </v-col>
              <v-col cols="12" lg="6" md="6">
                <v-col cols="12" lg="12" md="12" class="px-0 pb-0 pt-1">
                  <v-text-field
                    v-model="form.email"
                    class="ghx-form-input"
                    label="Email"
                    :rules="[rules.required, rules.email]"
                    outlined
                  ></v-text-field>
                </v-col>
                <v-col cols="12" lg="12" md="12" class="px-0 pb-0 pt-1">
                  <v-text-field
                    v-model="form.first_name"
                    class="ghx-form-input"
                    label="First Name"
                    outlined
                  ></v-text-field>
                </v-col>
                <v-col cols="12" lg="12" md="12" class="px-0 pb-0 pt-1">
                  <v-text-field
                    v-model="form.last_name"
                    class="ghx-form-input"
                    label="Last Name"
                    outlined
                  ></v-text-field>
                </v-col>
                <v-col cols="12" lg="12" md="12" class="px-0 pb-0 pt-1">
                  <v-text-field
                    v-model="form.zip_code"
                    class="ghx-form-input"
                    label="Zip Code"
                    :rules="[rules.zip_code]"
                    outlined
                  ></v-text-field>
                </v-col>
              </v-col>
            </v-row>
          </v-card-text>
        </v-card>
      </v-col>
    </v-row>
  </v-container>
</template>

<script>
import { SAVE_AND_EXIT } from "@/constants/questions";
import { questionnaire } from "@/store/main";
import Util from "@/helpers/Util";

const ZIP_CODE_NUMBER_LENGTH = 6;
const PHONE_NUMBER_LENGTH = 14;

export default {
  name: "SaveAndExit",

  setup() {},

  props: {
    isShow: {
      type: Boolean,
      default: false,
    },
  },

  data() {
    return {
      el: 1,
      form: {
        first_name: "",
        last_name: "",
        zip_code: "",
        email: "",
      },
      rules: {
        required: (value) => !!value || "This field is required.",
        email: (value) => {
          const pattern =
            /^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
          return pattern.test(value) || "Invalid e-mail.";
        },
        zip_code: (value) => {
          var upcase = value.toUpperCase();
          var x = upcase
            .replace(/[^a-zA-Z0-9]/g, "")
            .match(/([a-zA-Z0-9_-]{0,6})/);
          this.form.zip_code = x[1];

          return this.form.zip_code.length === ZIP_CODE_NUMBER_LENGTH ||
            this.form.zip_code.length === ZIP_CODE_NUMBER_LENGTH - 1
            ? true
            : "Invalid zip code.";
        },
      },
      store: questionnaire(),
      skip: false,
    };
  },

  computed: {
    pageDetails() {
      return this.store.config?.config?.saveForLater;
    },

    isEmptyForm() {
      return this.emptyForms ? true : false;
    },

    emptyForms() {
      /*var hasEmptyObj = []
        Object.keys(this.form).forEach((i) => {
          if (this.form[i] === '') {
            hasEmptyObj.push(i)
          }
        })
        return hasEmptyObj*/
      return this.form.email === "";
    },
    validEmail() {
      return this.rules.email(this.form.email) === true ? true : false;
    },
    validPhoneNumber() {
      return this.form.phone_number.length === PHONE_NUMBER_LENGTH;
    },
    validZipCode() {
      return (
        this.form.zip_code.length === ZIP_CODE_NUMBER_LENGTH ||
        this.form.zip_code.length === ZIP_CODE_NUMBER_LENGTH - 1
      );
    },
    storeForm() {
      return this.store.getForm;
    },
  },

  watch: {
    isEmptyForm: {
      handler(val) {
        if (!val && this.validEmail) {
          var resource = {
            selected: this.form,
            resource: this.questionItem,
          };
          this.$emit("on-item-selected", resource);
        } else {
          this.$emit("on-item-emptied");
        }
      },
      deep: true,
    },

    isShow(data) {
      if (!data) return;

      if (this.isEmptyForm) this.$emit("on-item-emptied");
      else {
        if (this.validEmail && this.validZipCode && this.validPhoneNumber) {
          var resource = {
            selected: this.form,
            resource: this.questionItem,
          };
          this.$emit("on-item-selected", resource);
        } else {
          this.$emit("on-item-emptied");
        }
        var resource = {
          selected: this.form,
          resource: this.questionItem,
        };
        this.$emit("on-item-selected", resource);
      }
    },

    form: {
      handler(val) {
        this.store.updateForm(val);
        this.skip = true;
      },
      deep: true,
    },

    storeForm(val) {
      if (this.skip) {
        this.skip = false;
        return;
      }

      this.form = {
        ...this.form,
        ...val,
      };
    },
  },

  methods: {
    onRestart() {
      const ga_btn_exit = this.pageDetails?.googleAnalytics?.find(
        (item) => item.type === "btnRestart"
      );

      Util.generateGoogleAnalytics(ga_btn_exit.value, this.pageDetails);

      this.$emit("on-reset");
    },
  },
};
</script>

<style lang="scss" scoped></style>
