import Vue from 'vue';
import Vuetify from 'vuetify/lib/framework';

import colors from 'vuetify/lib/util/colors'

Vue.use(Vuetify);

export default new Vuetify({
  theme: {
    themes: {
      light: {
        primary: '#6548c6', // #E53935
        secondary: '#e9e2ff', // #FFCDD2
        navy: '#29313E', // #FFCDD2
        accent: colors.indigo.base, // #3F51B5
        previousBtn: '#cecbe3',
        nextBtn: '#8cc93d'
      },
    },
  }
});
