<template>
  <ul>
    <li v-for="(v, n) in item" :key="n">{{n}}: 
      <span v-if="typeof v === 'object'">
        <list-comp :item="v"></list-comp>
      </span>
      <span v-else> {{v}} </span>
    </li>
  </ul>
</template>

<script>
export default {
  props: {
    item: {
      type: Object,
      default: () => {}
    }
  }
}
</script>