<template>
  <v-container class="ghx-audigy-app ma-0 pa-0">
    <!-- <v-btn class="ghx-open-hearing-screener-test" @click="dialog = true">Online Hearing Test</v-btn> -->

    <v-dialog
      v-model="dialog"
      :content-class="loader ? 'prevent-click' : ''"
      persistent
      transition="dialog-bottom-transition"
      max-width="1196"
      width="100%"
    >
      <AudigySteps
        :key="key"
        :defaultLastPage="el"
        @on-close="dialog = false"
        @on-reset="resetModal"
        @on-show-loader="loader = !loader"
      />
    </v-dialog>

    <!--<v-overlay 
      :value="loader"
      :z-index="-1"
      color="#AAA">
      
      <v-progress-circular
        indeterminate
        size="64"
      ></v-progress-circular>
    </v-overlay>-->
  </v-container>
</template>
<script>
import AudigySteps from "./AudigySteps.vue";
import Audigy from "@/models/Audigy";
import Util from "@/helpers/Util";
import { questionnaire } from "@/store/main";

export default {
  name: "AudigyApp",
  setup() {
    const store = questionnaire();

    return {
      store,
    };
  },

  components: {
    AudigySteps,
  },

  data: () => ({
    dialog: false,
    loader: false,
    key: Date.now(),
    el: 1,
  }),

  computed: {
    getUrlParams() {
      return Util.getUrlParams();
    },
  },

  watch: {
    dialog(data) {
      if (!data) document.querySelector("html").style.overflow = null;
      else {
        Util.generateGoogleAnalytics(
          { event: "online_hearing_test_click", }, 
          {}
        );

        // dataLayer.push({
        //   event: "online_hearing_test_click",
        // });

        document.querySelector("html").style.overflow = "hidden";
      }
    },
  },

  mounted() {
    this.registerForScreenTestTriggers();
    this.continueSavedSpot();
  },

  methods: {
    registerForScreenTestTriggers() {
      const modalTriggers = document.querySelectorAll(
        ".ghx-open-hearing-screener-test"
      );

      modalTriggers?.forEach((triggerElement) =>
        triggerElement.addEventListener("click", () => {
          const ga_btn_open = this.store.config?.config?.googleAnalytics[0]
          Util.generateGoogleAnalytics(ga_btn_open.value, {});
          this.dialog = true
        })
      );
    },

    async continueSavedSpot() {
      if (this.getUrlParams?.savedSpot) {
        var res = await Audigy.getSavedSpotData(this.getUrlParams?.savedSpot);

        if (res.data.userdata) {
          var data = res.data.userdata;
          this.store.setUserApiData(data.experimentData);
          this.store.setUserResponse(data.userData);
          this.dialog = true;
          this.el = parseInt(data.lastPage);
        }
      }
    },

    resetModal() {
      this.key = Date.now();
      this.store.resetUserApiData();
      this.store.resetUserResponse();
      this.store.resetSelectedFlow();
      //this.store.resetHzSelectedAudio();
    },
  },
};
</script>
