<template>
 	<p id="account-info">
		<span class="list-item-label" :list-id="listId">{{ listLabel }}</span>
		<span class="list-item-icon" @click="onInput"><i class="fa-solid fa-pencil"></i></span>
	</p>
</template>
 
<script>
export default {
 props: ['listLabel', 'listId'],
 methods: {
	    onInput(e) {
	      this.$emit("onInputChange", e.target);
	    },
	},
}
</script>