import {AxiosPost} from './Axios'

export default {
    
    createLPApi(path, data) {
        return this.isSiteURLSet() && AxiosPost.post(path, data)
    },
    
    createWPApi(data) {
            
        return this.isSiteURLSet() && AxiosPost.post('', data)
            
    },
    
    isSiteURLSet() {
        return window?.site_location_obj?.ajax_url
    }
}