<template>
  <v-container class="ghx-stepper-body pa-0">
    <v-row>
      <v-col cols="12" class="col-lg-12 col-sm-12">
        <v-card flat class="ghx-glass-background pa-0">
          <v-card-text class="text-center pa-0">
            <p
              class="ghx-stepper-content__question text--primary mb-0 pb-8"
              v-html="page.title"
            />
          </v-card-text>
          <v-row>
            <v-col cols="12" lg="6" md="6">
              <v-card-text class="ghx-subtitle text-left pt-0 px-0 pb-4">
                <p 
                  class="text-subtitle-1 font-weight-light"
                  v-html="page.message"
                >
                </p>
              </v-card-text>
            </v-col>
            <v-col cols="12" lg="6" md="6">
              <v-card-text class="pa-0 ghx-form">
                <v-row class="my-0">
                  <v-col
                    v-for="(field, index) in page.form.fields"
                    :key="index"
                    cols="12"
                    class="pb-0 pt-1"
                  >
                    <v-text-field
                      v-model="form[field.value]"
                      :disabled="disabled"
                      class="ghx-form-input"
                      :label="field.placeholder"
                      :rules="[rules.required, rules[field.value]]"
                      outlined
                      @keyup.enter="$emit('on-hit-enter', $event)"
                    >
                    </v-text-field>
                  </v-col>
                </v-row>
              </v-card-text>
            </v-col>
          </v-row>
        </v-card>
      </v-col>
    </v-row>
  </v-container>
</template>
<script setup>
import { ref, computed, watch, defineEmits } from "vue";
import { questionnaire } from "@/store/main";
import Util from "@/helpers/Util";

const emit = defineEmits([
  "on-item-selected", 
  "on-item-emptied", 
  "on-reset",
  "on-hit-enter"]);

const props = defineProps({
  isShow: {
    type: Boolean,
    default: () => false,
  },
  page: {
    type: Object,
    default: () => {},
  },
  currentOrder: {
    type: Number,
    default: () => 0,
  },
  disabled: {
    type: Boolean,
    default: () => false,
  },
});

const store = questionnaire();
const ZIP_CODE_NUMBER_LENGTH = 6;
const PHONE_NUMBER_LENGTH = 14;

const form = ref({
  first_name: "",
  last_name: "",
  zip_code: "",
  email: "",
  phone_number: "",
});

const rules = ref({
  required: (value) => !!value || "This field is required.",
  first_name: (value) => true,
  last_name: (value) => true,
  email: (value) => {
    const pattern =
      /^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
    return pattern.test(value) || "Invalid e-mail.";
  },
  phone_number: (value) => {
    var x = value.replace(/\D/g, "").match(/(\d{0,3})(\d{0,3})(\d{0,4})/);
    form.value.phone_number = !x[2]
      ? x[1]
      : "(" + x[1] + ") " + x[2] + (x[3] ? "-" + x[3] : "");
    return form.value.phone_number.length === PHONE_NUMBER_LENGTH
      ? true
      : "Invalid phone number.";
  },
  zip_code: (value) => {
    var upcase = value.toUpperCase();
    var x = upcase.replace(/[^a-zA-Z0-9]/g, "").match(/([a-zA-Z0-9_-]{0,6})/);
    form.value.zip_code = x[1];

    return form.value.zip_code.length === ZIP_CODE_NUMBER_LENGTH ||
      form.value.zip_code.length === ZIP_CODE_NUMBER_LENGTH - 1
      ? true
      : "Invalid zip code.";
  },
});

const skip = ref(false);

const config_object = computed(() => store.config);
const config = computed(() => config_object.value.config);
const page = computed(() => props.page);
const currentOrder = computed(() => props.currentOrder);

const emptyForms = computed(() => {
  let hasEmptyObj = [];
  Object.keys(form.value).forEach((i) => {
    if (form.value[i] === "") {
      hasEmptyObj.push(i);
    }
  });
  return hasEmptyObj;
});
const isEmptyForm = computed(() => {
  return emptyForms.value.length > 0 ? true : false;
});
const validEmail = computed(() => rules.value.email(form.value.email) === true);
const validPhoneNumber = computed(
  () => form.value.phone_number.length === PHONE_NUMBER_LENGTH
);
const validZipCode = computed(
  () =>
    form.value.zip_code.length === ZIP_CODE_NUMBER_LENGTH ||
    form.value.zip_code.length === ZIP_CODE_NUMBER_LENGTH - 1
);
const storeForm = computed(() => store.getForm);
//const isShow = computed(() => props.isShow);

watch(
  () => [
    isEmptyForm.value,
    validEmail.value,
    validPhoneNumber.value,
    validZipCode.value,
  ],
  ([val]) => {
    if (
      !val &&
      validEmail.value &&
      validPhoneNumber.value &&
      validZipCode.value
    ) {
      var resource = {
        selected: form.value,
        resource: page.value,
      };
      
      emit("on-item-selected", resource);
    } else {
      emit("on-item-emptied");
    }
  }
);

// watch(
//   () => isShow.value,
//   (val) => {
//     if (!val) return;

//     if (
//       !isEmptyForm.value &&
//       validEmail.value &&
//       validPhoneNumber.value &&
//       validZipCode.value
//     ) {
//       var resource = {
//         selected: form.value,
//         resource: page.value,
//       };
//       emit("on-item-selected", resource);
//     } else {
//       emit("on-item-emptied");
//     }
//   }
// );

watch(
  form,
  (val) => {
    store.updateForm(val);
    skip.value = true;
  },
  { deep: true }
);

watch(
  () => storeForm.value,
  (val) => {
    if (skip.value) {
      skip.value = false;
      return;
    }

    form.value = {
      ...form.value,
      ...val,
    };
  }
);

function onRestart() {
  const ga_btn_exit = page.value?.googleAnalytics?.find(
    (item) => item.type === "btnRestart"
  );

  Util.generateGoogleAnalytics(ga_btn_exit.value, page.value);
  emit("on-reset");
}
</script>
