import { defineStore } from 'pinia'
import Util from '@/helpers/Util'

export const questionnaire = defineStore('questionnaire', {
  state: () => ({
    config: require('../../config-ghx.json'),
    userResponse: [],
    userApiData: {
      Left: null,
      Right: null
    },
    selectedFlow: {},
    audioFiles: {
      500: require('../assets/audio/warble/warble_500.wav'),
      1000: require('../assets/audio/warble/warble_1000.wav'),
      2000: require('../assets/audio/warble/warble_2000.wav'),
      4000: require('../assets/audio/warble/warble_4000.wav')
    },
    // hzSelectedAudio: {},
    form: {
      first_name: '',
      last_name: '',
      zip_code: '',
      email: ''
    },
    ghxSettings: {},
  }),
  getters: {
    getUserResponse: (state) => {
      return state.userResponse
    },
    getSelectedFlow: (state) => {
      return state.selectedFlow
    },
    getUserApiData: (state) => {
      return state.userApiData
    },
    getAudioFiles: (state) => {
      return state.audioFiles
    },
    getForm: (state) => {
      return state.form
    },
    getGHXSettings: (state) => {
      return state.ghxSettings
    },
    // getHzSelectedAudio: (state) => {
    //   return state.hzSelectedAudio
    // }
  },
  actions: {
    // setHzSelectedAudio(data, hz) {
    //   let temp = [data]
    //   this.hzSelectedAudio[hz] = this.hzSelectedAudio[hz] || []
    //   this.hzSelectedAudio[hz] = this.hzSelectedAudio[hz].concat(temp)
    // },
    
    setGHXSettings(data) {
      this.ghxSettings = data
    },
    
    addUserResponse(data) {
      this.userResponse[data.field] = data.value
    },
    
    addUserApiData(data) {
      this.userApiData[data.key] = data.value;
    },
/*    setUserApiData(data) {
      this.userApiData = data;
    },*/
    mergeUserApiData(data, ear) {
      ear = Util.capitalizeFirstLetter(ear);
      
      this.userApiData[ear] = {
        ...this.userApiData[ear],
        ...data
      };
      
      if (ear && data?.experiment?.previousTrials)
        this.userApiData[ear].previousTrials = data.experiment.previousTrials
        
    },
    
    setUserApiData (data) {
      this.userApiData = data;
    },
    
    resetUserApiData () {
      this.userApiData = {
        Left: null,
        Right: null
      };
    },
    
    setAudioFiles(data) {
      if (data.value)
        this.audioFiles[data.key] = data.value;
    },
    
    setSelectedFlow(data) {
      this.selectedFlow[data.key] = data.value;
      //this.selectedFlow = data
      //this.userResponse = []
    },
    
    setUserResponse (data) {
      this.userResponse = data
    },
    
    resetUserResponse () {
      this.userResponse = []
    },
    
    resetSelectedFlow () {
      this.selectedFlow = {}
    },
    
    // resetHzSelectedAudio () {
    //   this.hzSelectedAudio = {}
    // },
    
    updateForm (data) {
      this.form = {
        ...this.form,
        ...data
      }
    }
  },
})

export const leadProcessor = defineStore('leadProcessor', {
    state: () => ({
      token: null
    }),
    
    getters: {
      getToken: (state) => {
        return state.token
      },
    },
    
    actions: {
      setToken (data) {
        this.token = data
      }
    }
})